<template>
  <section style="width: 100%; height: 100%">
    <GmapMap
        id="map"
        ref="mapRef"
        v-if="cebuPosition"
        :center="cebuPosition"
        :zoom="15"
        map-type-id="terrain"
        style="width: 100%; height: 100%"
    >
      <GmapMarker v-for="(plot, index) in restaurantPlots"
                  :key="index"
                  @click="openInfoWindowTemplate(index)"
                  :position="google && new google.maps.LatLng(plot.position[0], plot.position[1])"
      />
      <gmap-info-window
          :options="{
          maxWidth: 300,
          pixelOffset: { width: 0, height: -35 }
        }"
          :position="infoWindow.position"
          :opened="infoWindow.open"
          @closeclick="infoWindow.open=false">
        <div v-html="infoWindow.template"></div>
      </gmap-info-window>
      <GmapMarker v-if="hasEnableUserMarker"
                  :position="google && new google.maps.LatLng(userPlots.lat, userPlots.lng)"
      />
      <DirectionsRenderer
          travelMode="DRIVING"
          :origin="origin"
          :destination="destionation"/>
    </GmapMap>
  </section>
</template>

<script>
import {gmapApi} from 'vue2-google-maps'
import {supabase} from "@/lib/supabaseClient";
import DirectionsRenderer from "@/components/DirectionsRenderer";

export default {
  name: "CebuRestaurantView",
  components: {
    DirectionsRenderer
  },
  data() {
    return {
      hasEnableUserMarker: false,

      cebuPosition: {lat: 1, lng: 1},
      restaurantPlots: [],
      userPlots: {lat: 1, lng: 1},
      nextPageTokenApi: null,
      start: "",
      end: "",
      infoWindow: {
        position: {lat: 0, lng: 0},
        open: false,
        template: ''
      }
    }
  },
  methods: {
    retrieveNearbyRestaurants(map) {
      this.retrieveNearbyRestaurantsGoogleApi(map, null);
      console.log(this.restaurantPlots);
    },
    retrieveNearbyRestaurantsGoogleApi(map, temp_next_page_token) {
      let request = {
        type: ['restaurant'],
        radius: 5500,
        location: this.cebuPosition
      };

      if (null != temp_next_page_token) {
        request.pagetoken = temp_next_page_token;
      }

      console.log(request);

      let service = new this.google.maps.places.PlacesService(map);
      service.nearbySearch(request, (results, status, next_page_token) => {
        if (status === 'OK') {
          console.log(results);
          console.log(next_page_token);
          // setTimeout(() => {
          //   if (next_page_token.hasNextPage) {
          //     temp_next_page_token = next_page_token;
          //     console.log(next_page_token);
          //     this.retrieveNearbyRestaurantsGoogleApi(map, temp_next_page_token);
          //   }
          // }, 3000);
          for (let i = 0; i < results.length; i++) {
            this.restaurantPlots.push({
              position: [results[i].geometry.location.lat(), results[i].geometry.location.lng()]
            });
          }
        }
      });
    },
    async retrieveSpecialMeals() {
      const {data} = await supabase.from('special_food')
          .select()
      console.log(data);
    },
    async retrieveDefaultConfig() {
      const {data} = await supabase.from('default_config')
          .select()
          .eq('id', 1)
      console.log(data);
      this.cebuPosition = {
        lat: data[0].lat,
        lng: data[0].lng
      }

    },
    openInfoWindowTemplate(index) {
      const rp = this.restaurantPlots[index]
      this.infoWindow.position = {lat: rp.position[0], lng: rp.position[1]}
      this.infoWindow.template = `<b>some data</b><br>some data2</br>`
      this.infoWindow.open = true
    },
  },
  computed: {
    google: gmapApi,
    origin() {
      if (!this.start) return null;
      return this.start;
    },
    destionation() {
      if (!this.end) return null;
      return this.end;
    }
  },
  watch: {
    nextPageTokenApi(newT) {
      console.log(newT)
    }
  },
  mounted() {
    this.retrieveDefaultConfig().then(() => {
      this.$refs.mapRef.$mapPromise.then((map) => {
        this.retrieveNearbyRestaurants(map);
      })
      this.retrieveSpecialMeals();
      this.start = this.google && new this.google.maps.LatLng(10.3161138351106, 123.886463462485);
      // this.end = new this.google.maps.LatLng(plot.position[0], plot.position[1]);
      this.end = this.google && new this.google.maps.LatLng(10.311496555600227, 123.894086544846);
    });


  }
}
</script>

